import React from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import { Navbar, Container, Nav } from 'react-bootstrap';
import ProductList from './components/ProductList';
import ProductForm from './components/ProductForm';
import ClientList from './components/ClientList';
import ClientForm from './components/ClientForm';
import UserList from './components/UserList';
import UserForm from './components/UserForm';
import SaleList from './components/SaleList';
import SaleForm from './components/SaleForm';
import PurchaseList from './components/PurchaseList';
import PurchaseForm from './components/PurchaseForm';
import PrivateRoute from './components/PrivateRoute';
import CategoryForm from './components/CategoryForm';
import CategoryList from './components/CategoryList';
import Login from './components/login';


function App() {
    return (
        <Router>
            <Navbar bg="light" expand="lg">
                <Container>
                    <Navbar.Brand href="/">Point de Vente</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <Nav.Link as={Link} to="/">Home</Nav.Link>
                            <Nav.Link as={Link} to="/add-product">Add Product</Nav.Link>
                            <Nav.Link as={Link} to="/products">Product List</Nav.Link>
                            <Nav.Link as={Link} to="/clients">Client List</Nav.Link>
                            <Nav.Link as={Link} to="/users">User List</Nav.Link>
                            <Nav.Link as={Link} to="/sales">Sales</Nav.Link>
                            <Nav.Link as={Link} to="/purchases">Purchases</Nav.Link>
                            <Nav.Link as={Link} to="/categories">Categories</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <Container>
                <Routes>
                    <Route path="/login" element={<Login />} />
                    <Route path="/products" element={<PrivateRoute><ProductList /></PrivateRoute>} />
                    <Route path="/add-product" element={<PrivateRoute><ProductForm /></PrivateRoute>} />
                    <Route path="/clients" element={<PrivateRoute><ClientList /></PrivateRoute>} />
                    <Route path="/add-client" element={<PrivateRoute><ClientForm /></PrivateRoute>} />
                    <Route path="/users" element={<PrivateRoute><UserList /></PrivateRoute>} />
                    <Route path="/add-user" element={<PrivateRoute><UserForm /></PrivateRoute>} />
                    <Route path="/sales" element={<PrivateRoute><SaleList /></PrivateRoute>} />
                    <Route path="/add-sale" element={<PrivateRoute><SaleForm /></PrivateRoute>} />
                    <Route path="/purchases" element={<PrivateRoute><PurchaseList /></PrivateRoute>} />
                    <Route path="/add-purchase" element={<PrivateRoute><PurchaseForm /></PrivateRoute>} />
                    <Route path="/categories" element={<PrivateRoute><CategoryList /></PrivateRoute>} />
                    <Route path="/add-category" element={<PrivateRoute><CategoryForm /></PrivateRoute>} />
                </Routes>
            </Container>
        </Router>
    );
}

export default App;
